


.container{
    color: white;
}

.movie{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10rem;
}

.movie__details{
    display: flex;
    flex-direction: column;
    gap:1rem;
    height: 80vh;
    font-size: 20px;
}
.movie__contents{
    margin-top: 3rem;
    font-size: 20px;
    padding: 2rem;
    line-height: 40px;
}

.arrowLeft{
    cursor: pointer;
    font-size: 25px;
    border: 3px solid white;
    padding: 10px;
    border-radius: 10px;

}

.arrowLeft:hover{
    border: 3px solid #03CD5C;
    color: #03CD5C;
}