.movie{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 3px solid white;
    border-radius: 10px;
    padding: 1rem;
    -webkit-box-shadow: 7px 6px 14px 1px rgba(38,95,16,0.8); 
    box-shadow: 7px 6px 14px 1px rgba(38,95,16,0.8);
    transition: transform 0.2s ease;

}
.movie:hover{
    transform: scale(1.1);
}
.movie__title{
    text-align: center;
    margin: 0;
}

.movie__title a{
    color: white;
    text-decoration: none;
    transition: color 0.4s ease;

}
.movie__title a:hover{
    color: #03CD5C;
}

.movie img{
    width: 80%;
    height: 60%;
    margin: 2rem 0;
}

.movie ul{
    color: white;
    list-style: none;
    padding: 0;
    font-size: 1.2rem;
}

.movie__genres{
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;

}

.movie__genres strong{
    font-size: 22px;
}


